body {
  background-color: #ffffff;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center; }
  .App-title {
    font-weight: 600;
    font-size: 30px;
    color: #343a40;
    margin-right: 440px; }
  .App-subTitle {
    font-weight: 600;
    font-size: 24px;
    color: #343a40;
    margin-right: 235px; }
  .App-name {
    color: #858e96;
    margin-top: 40px;
    margin-right: 575px; }
  .App-github {
    margin-right: 664px;
    color: #858e96; }

.Input {
  text-align: center; }
  .Input-input {
    margin: 25px;
    width: 550px;
    border: 0;
    border-bottom: 1px solid #495057;
    background: none;
    text-align: center;
    font-size: 20px;
    outline: none;
    color: #495057; }

.Preview {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  height: 100%;
  margin-top: 30px; }
  .Preview-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 700px;
    height: 350px; }

.PaletteBox {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%; }
  .PaletteBox-wrap {
    margin-left: 65px;
    width: 600px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    list-style: none;
    margin-top: 0;
    margin-bottom: 0;
    padding: 0; }
  .PaletteBox-palettes {
    text-align: left;
    width: 150px;
    padding: 10px 0; }
  .PaletteBox-palette {
    font-size: 12px;
    letter-spacing: 0.05em;
    list-style: none;
    cursor: pointer; }
    .PaletteBox-palette-box {
      width: 14px;
      height: 14px;
      display: inline-block;
      margin-top: -2px;
      margin-right: 10px;
      border-radius: 3px;
      vertical-align: middle; }

.SaveButton {
  margin-top: 30px;
  margin-bottom: 60px; }
  .SaveButton-button {
    color: white;
    padding: 15px 30px;
    text-decoration: none;
    border-radius: 5px; }

